* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

.App > h1 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 36px;
  padding: 30px;
  margin-bottom: 30px;
  background-color: #06f;
  color: #fff;
  text-align: center;
}

.editor-app h2 {
  text-decoration: underline;
  margin: 0 10px 10px 0;
  display: inline-block;
}

.site {
  display: inline-block;
  color: #00f;
}

.site:hover {
  color: #00f;
}

.site:visited {
  color: #00f;
}

.container {
  max-width: 800px;
  margin: 20px auto;
}

.editor-app {
  margin-bottom: 40px
}

.cke_float {
  display: none !important;
}

.editor-preview {
  margin: 20px 0 40px;
}

.editor-preview .editor-type {
  color: #666;
  font-size: 18px;
  font-weight: bold;
  margin-top: 20px
}

.editor-preview .editor-result {
  font-size: 16px;
  color: #000;
  margin: 10px 0;
}